	module.exports = {"data":{"post":{"html":"<p>I've spent the past several weeks anticipating, then learning, that my job is going to become less comfortable. I may lose it, I may not.</p>\n<p>Today a colleague of my wife, 25 years with the company, learned the results of her breast biopsy. Cancer, as aggressive as it gets. Tomorrow she learns whether the lymph nodes are also infected, i.e. whether the cancer is already terminal or merely extremely critical.</p>","fields":{"title":"Perspective","date":"23 Aug 2004","url":"https://papascott.de/2004-08-23-perspective/"}}},"pathContext":{"slug":"/2004-08-23-perspective/","prev":{"fields":{"title":"15 Minutes of Concentration","date":"22 Aug 2004","slug":"/2004-08-22-15-minutes-of-concentration/"}},"next":{"fields":{"title":"Happy Birthday To Me","date":"24 Aug 2004","slug":"/2004-08-24-happy-birthday-to-me/"}}}}

